export default () => {
  const [isMobile, setIsmobile] = React.useState(false);

  const onResize = () => {
    const screenWidth = window.innerWidth;
    setIsmobile(screenWidth <= 960);
    const _isMobile = screenWidth <= 780;
    // if (_isMobile) {
    //   document.documentElement.setAttribute("class", "moblie");
    // } else {
    //   document.documentElement.setAttribute("class", "");
    // }

    document.documentElement.style.fontSize =
      Math.min(Math.max(document.body.clientWidth, 320) / 7.5, 100) + "px";

    setImmediate(_isMobile);
  };
  const { run } = useThrottleFn(onResize, { wait: 300 });

  React.useEffect(() => {
    run();
    window.addEventListener("resize", run);
    return () => {
      window.removeEventListener("resize", run);
    };
  }, [run]);

  return <App isMobile={isMobile} />;
};
